import { useEffect } from "react";
import { CanvasPlugin } from "../../type";
import { redo, undo } from "../../misc";

class ShortcutPlugin implements CanvasPlugin {

  hotKeyConfig = [
    {
      key: 'cmd-z',
      callback: (e) => undo(),
    },
    {
      key: 'cmd-shift-z',
      callback: (e) => redo(),
    }
]

  render() {

    useEffect(() => {

      const handleKeyDown = (event: KeyboardEvent) => {
        this.hotKeyConfig.forEach(({ key, callback }) => {
          const combo = key.split('-').map(k => k.toLowerCase());
          const conditions = [
            combo.includes('ctrl') === event.ctrlKey,
            combo.includes('alt') === event.altKey,
            combo.includes('shift') === event.shiftKey,
            (combo.includes('meta') || combo.includes('cmd')) === event.metaKey,
            combo.some(_key => 
              !['ctrl', 'alt', 'shift', 'meta'].includes(_key) && 
              _key === event.key.toLowerCase()
            )
          ];
          
          if (conditions.every(Boolean)) {
            event.preventDefault();
            callback(event);
          }
        })
      };

  
      document.addEventListener('keydown', handleKeyDown);
      return () => document.removeEventListener('keydown', handleKeyDown);
    }, [this.hotKeyConfig]);

    return null;
  }
} 

export default ShortcutPlugin;