import Button from "./Button";
import Paper from "./Paper";
import Input from "./Input";
import { contextMenuWrapper, dragWrapper, dropWrapper, eventsWrapper, gridLayerWrapper, statesWrapper } from "./wrappers";


const wrapperList: any[] = [
  dropWrapper, 
  dragWrapper, 
  eventsWrapper, 
  contextMenuWrapper, 
  gridLayerWrapper, 
  statesWrapper
];

const Components = {
  input: {
    Component: Input,
    RawComponent: Input,
    type: 'input',
    schema: (Input as any).schema,
  },
  button: {
    Component: Button,
    RawComponent: Button,
    type: 'button',
    schema: (Button as any).schema,
  },
  paper: {
    Component: Paper,
    RawComponent: Paper,
    type: 'paper',
    schema: (Paper as any).schema,
  },
};

function process() {
  Object.keys(Components).forEach(key => {
    let Component = Components[key].Component;
    wrapperList.forEach(wrapper => {
      Component = wrapper(Component);
    });
    Components[key].Component = Component;
  });
}

process();

export default Components;